import React from 'react';
import { Frame, WidgetConfig } from '@piloteers/dashboard-model';
import './grid.styles.scss';

const ENV = require('environment');
interface IProps {
  frame: Frame;
  [key: string]: any;
}

interface IState {
  grid2D: Array<any>;
}

export class Grid extends React.Component<IProps, IState> {
  constructor(props: IProps) {
    super(props);

    this.state = {
      grid2D: []
    };
  }

  componentWillMount() {
    this.renderGrid();
  }

  componentDidUpdate(props: IProps, state) {
    if (this.props.frame.updated != props.frame.updated) {
      this.renderGrid();
    }
  }

  renderGrid() {
    let grid2D = [];
    let x = 0;
    let y = 0;
    const { frame } = this.props;

    // build 2d grid array with null values
    frame.widgetConfigs.map(wc => {
      while (y < wc.y + wc.rows) {
        grid2D.push([]);
        y++;
      }

      grid2D.map((d, i) => {
        if (wc.x + wc.cols > x) {
          x = wc.x + wc.cols;
        }
        for (let index = 0; index < x; index++) {
          if (grid2D[i][index] === '.') {
          } else {
            grid2D[i].push('.');
          }
        }
      });
    });

    // add missing
    for (let n1 = 0; n1 < frame.gridRows; n1++) {
      if (!grid2D[n1]) {
        grid2D.push([]);
      }
      for (let n2 = 0; n2 < frame.gridColumns; n2++) {
        if (!grid2D[n1][n2]) {
          grid2D[n1].push('.');
        }
      }
    }

    // fill array with ids
    frame.widgetConfigs.map(wc => {
      for (let i2 = wc.y; i2 < wc.y + wc.rows; i2++) {
        for (let i1 = wc.x; i1 < wc.x + wc.cols; i1++) {
          grid2D[i2][i1] = 'area-' + wc.resourceId;
        }
      }
    });

    this.setState({ grid2D });
  }

  getTemplateAreas(grid2D) {
    return "'" + grid2D.map(arr => arr.join(' ')).join("' '") + "'";
  }

  render() {
    const { grid2D } = this.state;
    const { frame } = this.props;
    return (
      <div className='plt-grid' style={{ gridTemplateAreas: this.getTemplateAreas(grid2D) }}>
        {frame.widgetConfigs.map((ws: WidgetConfig | any, i) => {
          const isPlaceholder = ws.widgetId == 'd7c84320-b849-11e9-9116-4362fe3decbc' ? true : false;
          return (
            <div
              className={`plt-grid__widget-area ${isPlaceholder ? 'plt-grid__widget-area--placeholder' : ''}`}
              key={i}
              style={{ gridArea: 'area-' + ws.resourceId }}
            >
              {!isPlaceholder ? (
                <iframe
                  key={ws.updated}
                  src={`${ENV.BACKEND_URL}/widget?widgetId=${ws.widgetId}&frameId=${frame.resourceId}&widgetConfigId=${ws.resourceId}`}
                />
              ) : null}
            </div>
          );
        })}
      </div>
    );
  }
}
